<template>
  <div id="app">
    <el-container>
      <el-header style="color: rgb(222,222,222)">生成shell命令

      </el-header>
      <el-main>
        <div class="app-inp">
          <div class="el-inp-box">
            <el-input placeholder="请输入内容" v-model="ip">
              <template slot="prepend">Ip</template>
            </el-input>
          </div>
          <div class="el-inp-box">
            <el-input placeholder="请输入内容" v-model="port">
              <template slot="append">Port</template>
            </el-input>
          </div>
          <el-button type="primary" icon="el-icon-plus"
                     style="height: 40px;margin: 5px" @click="dialogFormVisible = true,dialogTitle='添加shell指令'">添加
          </el-button>
        </div>


        <div v-for="(item,index) in firstData" :key="index" class="card-box"
             @click="copyLink(item,lastData[index])" @mouseover="overRun($event)" @mouseleave="leaveRun($event)">
          <!--     删除     -->
          <span style="display: none;" class="env-del-btn-span el-icon-delete" @click="domDele(index)"></span>
          <!--     修改     -->
          <span style="display: none;" class="env-del-btn-span el-icon-edit-outline" @click="domRevise(index)"></span>
          <el-tooltip class="item" effect="dark" content="点击复制" placement="top">
            <el-scrollbar style="height:100%">
              <p style="font-size: 14px;color: rgba(222,222,222);">{{ appData[index].name }}</p>
              <span ref="copy" class="item text">
                {{ item }}{{ ip }}:{{ port }}{{ lastData[index] }}
              </span>
              <br/>
            </el-scrollbar>
          </el-tooltip>
        </div>
      </el-main>
    </el-container>

    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" @close="dialog()">
      <el-input
          type="textarea"
          autosize
          placeholder="name"
          v-model="tempName">
      </el-input>
      <div style="margin: 20px 0;"></div>
      <el-input
          type="textarea"
          autosize
          placeholder="shell"
          v-model="tempShell">
      </el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogFormVisible = false,addDataRun(),openFullScreen()">确 定
        </el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>

// import
export default {
  name: 'App',
  components: {},
  data() {
    return {
      ip: '192.168.1.1',
      port: '8088',
      appData: [], // json shell指令
      nameData: [],
      firstData: [],
      lastData: [],
      dialogFormVisible: false,
      tempName: '',
      tempShell: '',
      dialogTitle: '添加shell指令',
      shellIndex: Number,
      // fullscreenLoading: false
    }
  },
  mounted() { // vue挂载完成
    // localStorage.setItem('shell', JSON.stringify([]))
    let shell = JSON.parse(localStorage.getItem('shell'))
    if (shell !== null) {
      this.shellLocal(shell)
    }
  },
  methods: {
    run() { // 初始函数
      this.firstData = []
      this.lastData = [] // 滞空
      this.appData.forEach((res) => {
        let tempData = res.shell
        let tn_first = tempData.search("#ip") // 地址前面的长度
        let tn_last = Number(tn_first + 3) // 地址后面的长度
        this.firstData.push(tempData.slice(0, tn_first))
        this.lastData.push(tempData.slice(tn_last))
      })
    },
    copyLink(first, last) { // 复制
      let text = first + this.ip + ':' + this.port + last
      let input = document.createElement('input');
      input.setAttribute('readonly', 'readonly'); // 防止手机上弹出软键盘
      input.setAttribute('value', text);
      document.body.appendChild(input);
      // input.setSelectionRange(0, 9999);
      input.select();
      let res = document.execCommand('copy');
      document.body.removeChild(input);
      return res;
    },
    shellLocal() { // 读数据
      // console.log(JSON.parse(localStorage.getItem('shell')))
      this.appData = JSON.parse(localStorage.getItem('shell'))
      this.run()
    },
    addDataRun() { // 添加 修改 数据
      let temp = {
        "name": this.tempName,
        "shell": this.tempShell
      }
      if (this.dialogTitle === '添加shell指令') {
        this.appData.push(temp)
      } else {
        this.appData[this.shellIndex] = temp
      }
      localStorage.setItem('shell', JSON.stringify(this.appData))

      this.run()
    },
    // eslint-disable-next-line no-unused-vars
    overRun(event) { // 鼠标移入
      //获取 删除和修改 元素
      const dom = event.currentTarget.getElementsByClassName('env-del-btn-span')
      dom[0].style.cssText += "display:block"
      dom[1].style.cssText += "display:block"
    },
    // eslint-disable-next-line no-unused-vars
    leaveRun(event) { // 鼠标移出
      const dom = event.currentTarget.getElementsByClassName('env-del-btn-span')
      dom[0].style.cssText += "display:none"
      dom[1].style.cssText += "display:none"
    },
    domDele(index) { // 删除 dialogTitle
      localStorage.setItem('shell', JSON.stringify(this.$delete(this.appData, index)))
      this.run()
    },
    domRevise(index) { // 修改
      // console.log(this.appData[index].name)
      this.dialogTitle = '修改' + this.appData[index].name
      const temp = {
        "name": this.appData[index].name,
        "shell": this.appData[index].shell
      }
      this.tempName = temp.name
      this.tempShell = temp.shell
      // 全局下标 此次修改的index
      this.shellIndex = index
      this.dialogFormVisible = true

    },
    openFullScreen() { // 全局加载1
      const loading = this.$loading({
        lock: true,
        text: '加载中...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      setTimeout(() => {
        loading.close();
        this.$message({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        });
      }, 800);

    },
    dialog() { // 弹出框关闭时的回调
      this.tempName = ''
      this.tempShell = ''
    }

  }
}
</script>

<style>
@import './assets/app.css';

</style>
